import React, {useState} from "react"
import {Row, Col, Input, CustomInput, Modal} from 'reactstrap'
import Rating from 'react-rating'
import {Star} from 'react-feather'
import Avatar from '@components/avatar'
import notFoundImage from '@src/assets/images/svg/not-found.svg'
import ModalAnswerRating from "./components/ModalAnswerRating"

// pass the rating list to this component to view the ratings
const RatingList = ({rating}) => {
    const emptyList = false
    const [isShowModalAnswerRating, onSetModalAnswerRating] = useState(false)
    const [selectedRating, setSelectedRating] = useState({})
    const [ratingList, setRatingList] = useState(rating)
    const item5Counter = rating.filter(({starPoint}) => starPoint === 5).length
    const item4Counter = rating.filter(({starPoint}) => starPoint === 4).length
    const item3Counter = rating.filter(({starPoint}) => starPoint === 3).length
    const item2Counter = rating.filter(({starPoint}) => starPoint === 2).length
    const item1Counter = rating.filter(({starPoint}) => starPoint === 1).length

    const onChangeFilter = (e) => {
        const value = parseInt(e.currentTarget.value)
        if (value) {
            setRatingList(rating.filter(({starPoint}) => starPoint === value))
        }
    }

    return (
        <>
            <div className={'mt-2'}>
                <Row>
                    <Col md={3}>
                        <CustomInput className='form-control' type='select' id='view-type' onChange={onChangeFilter}>
                            <option value="all">Tất cả</option>
                            <option value="new">Mới nhất</option>
                            <option value="5">5 sao ({item5Counter})</option>
                            <option value="4">4 sao ({item4Counter})</option>
                            <option value="3">3 sao ({item3Counter})</option>
                            <option value="2">2 sao ({item2Counter})</option>
                            <option value="1">1 sao ({item1Counter})</option>
                        </CustomInput>
                    </Col>
                </Row>
                {/*EMPTY LIST*/}
                {
                    ratingList.length === 0 &&
                    <div className={'text-center'}>
                        <p>Không có đánh giá nào!</p>
                        <img src={notFoundImage} alt="not found"/>
                    </div>
                }
                {/*EMPTY LIST*/}
                {
                    ratingList &&
                    <div className={'border mt-2'}>
                        {ratingList.map((item, index) => {
                            return (
                                <div className="m-2 rating-item border-bottom" key={index}>
                                    <div className="container-fluid py-1">
                                        <Row>
                                            <Col md={3}>
                                                <div className="d-flex">
                                                    <div>
                                                        <Avatar size='md' img={item.avatar}/>
                                                    </div>
                                                    <div className={'ml-1'}>
                                                        <h5>{item.name}</h5>
                                                        <div>Đã mua sản phẩm</div>
                                                        <div><label>Size: </label><span
                                                            className={'font-weight-bold'}> {item.size}</span></div>
                                                        <div><label>Màu sắc: </label><span
                                                            className={'font-weight-bold'}> {item.color}</span>
                                                        </div>
                                                        <div><label>Cao: </label><span
                                                            className={'font-weight-bold'}> {item.height}cm</span></div>
                                                        <div><label>Cân nặng: </label><span
                                                            className={'font-weight-bold'}> {item.weight}kg</span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </Col>
                                            <Col md={9}>
                                                <div className="d-flex justify-content-between">
                                                    <div>
                                                        <div>
                                                            <Rating
                                                                emptySymbol={<Star size={15} fill={'transparent'}
                                                                                   stroke={'#babfc7'}/>}
                                                                fullSymbol={<Star size={15} fill={'#FFA113'}
                                                                                  stroke={'#FFA113'}/>}
                                                                initialRating={item.starPoint} readonly={true}/>
                                                            <span className={'ml-1'}>{item.starComment}</span>
                                                        </div>
                                                        <p className={'mt-1'}>
                                                            {item.comment}
                                                        </p>
                                                        <div className="images">
                                                            {
                                                                item.images && item.images.map((item, index) => {
                                                                    return (
                                                                        <img src={item} alt="" key={index}
                                                                             className={'mr-1'}/>
                                                                    )
                                                                })
                                                            }
                                                        </div>
                                                        <div className="mt-1 rating-date">{item.date}</div>
                                                        <button
                                                            onClick={() => { onSetModalAnswerRating(!item.answered); setSelectedRating(item) }}
                                                            className="btn p-0 mt-1 text-underline">
                                                            {item.answered ? 'Đã trả lời' : 'Chưa trả lời'}
                                                        </button>
                                                        {
                                                            item.answered &&
                                                            <p className="bg-primary p-1 mt-1 text-white rounded">{item.answered}</p>
                                                        }
                                                    </div>
                                                    <div className={'ml-3'}>
                                                        <button className={'btn btn-primary white-space-nowrap'}>
                                                            Hiển thị lên
                                                        </button>
                                                    </div>
                                                </div>
                                            </Col>
                                        </Row>
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                }
            </div>
            <Modal isOpen={isShowModalAnswerRating} className='modal-dialog-centered' toggle={() => { onSetModalAnswerRating(false) }}>
                <ModalAnswerRating ratingDetail={selectedRating}/>
            </Modal>
        </>
    )
}

export default RatingList
