import React from "react"
import Avatar from '@components/avatar'

const ModalAnswerRating = ({ratingDetail}) => {
    return (
        <>
            {
                ratingDetail &&
                <div className={'p-2 d-flex'}>
                    <div>
                        <Avatar size={'lg'} img={ratingDetail.avatar}/>
                    </div>
                    <div className={'pl-2 w-100'}>
                        <h5>{ratingDetail.name}</h5>
                        <p className={'modal-rating-comment'}>{ratingDetail.comment}</p>
                        <textarea placeholder={'Nội dung trả lời'} className={'form-control'}/>
                        <div className="text-right mt-1">
                            <button className="btn btn-primary">Hoàn thành</button>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

export default ModalAnswerRating
