// ** Styles
import React, {Fragment, useState, useEffect} from 'react'
import '@styles/react/apps/app-users.scss'
import {
    Card,
    CardHeader,
    CardTitle,
    CardBody,
    Label,
    Input,
    Row,
    Col,
    Nav,
    NavItem,
    NavLink,
    TabContent, TabPane
} from "reactstrap"
import classnames from "classnames"
import RatingList from "./RatingList"
import './styles/rating.scss'
import img1 from '@src/assets/images/portrait/small/avatar-s-1.jpg'

const demoRating = [
    {
        avatar: img1,
        name: 'Name',
        size: 'XL',
        color: 'Xám',
        height: 172,
        weight: 67,
        starPoint: 5,
        starComment: 'rộng',
        images: [img1,  img1],
        status: 0, // status case 0: not published; case: 1 published; case: 2 reject
        comment: 'Áo chất liệu lụa lụa bóng bóng nhẹ, k quá nóng đâu.\n' +
            'Mình nữ m6 đặt cho ny size L nhưng mình mặc vừa đẹp nên bếch luôn =)))\n' +
            'Săn được flashsale còn có tầm 100k nhưng vẫn được đóng hộp cuộn giấy gói tử tế. ',
        date: '21/06/2021',
        answered: 'Feaer rất vui khi nhận được những phản hồi từ bạn. Cảm ơn bạn đã tin tưởng \n' +
            'cũng như lựa chọn sản phẩm bên mình'
    },
    {
        avatar: img1,
        name: 'Name 2',
        size: 'M',
        color: 'Trắng',
        height: 172,
        weight: 67,
        starPoint: 3,
        starComment: 'rộng',
        images: [img1,  img1],
        status: 0, // status case 0: not published; case: 1 published; case: 2 reject
        comment: 'Áo đẹp lắm đáng mua, hài lòng,...Đóng hộp xịn xò lắm y như unbox đồ hiệu\n' +
            'lần sau sẽ ủng hộ nhiều nữa, shop cập nhật thêm nhiều mẫu mã mới nữa nhé.',
        date: '07/05/2021',
        answered: ''
    }
]


const Ratings = () => {
    const [activeTab, setActiveTab] = useState('1')
    const toggle = tab => {
        if (activeTab !== tab) setActiveTab(tab)
    }

    const demoAnsweredRatingList = demoRating.filter(({answered}) => answered !== '')
    const demoNotAnsweredRatingList = demoRating.filter(({answered}) => answered === '')

    return (
        <>
            <Fragment>
                <Card>
                    <CardHeader>
                        <CardTitle tag='h4'>Đánh giá sản phẩm</CardTitle>
                    </CardHeader>
                    <CardBody>
                        <Row>
                            <Col md={6}>
                                <Label className='mb-0 white-space-nowrap' for='search-invoice'>
                                    Tên sản phẩm
                                </Label>
                                <Input
                                    className='w-100'
                                    type='text'
                                />
                            </Col>
                            <Col md={4}>
                                <Label className='mb-0 white-space-nowrap' for='search-invoice'>
                                    Thời gian
                                </Label>
                                <Input
                                    className='w-100'
                                    type='date'
                                />
                            </Col>
                        </Row>
                        <div className="text-right mt-1">
                            <button className="btn btn-primary px-5">Tìm</button>
                        </div>
                        <Nav tabs className={'menu-sub-tab mt-3'}>
                            <NavItem>
                                <NavLink className={classnames({active: activeTab === '1'})}
                                         onClick={() => toggle('1')}>Tất cả({demoRating.length})</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={classnames({active: activeTab === '2'})}
                                         onClick={() => toggle('2')}>Đã trả lời({demoAnsweredRatingList.length})</NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink className={classnames({active: activeTab === '3'})}
                                         onClick={() => toggle('3')}>Chưa trả lời({demoNotAnsweredRatingList.length})</NavLink>
                            </NavItem>
                        </Nav>
                        <TabContent activeTab={activeTab} className={'tab-sub-content'}>
                            <TabPane tabId={'1'}>
                                <RatingList rating={demoRating}/>
                            </TabPane>
                            <TabPane tabId={'2'}>
                                <RatingList rating={demoAnsweredRatingList}/>
                            </TabPane>
                            <TabPane tabId={'3'}>
                                <RatingList rating={demoNotAnsweredRatingList}/>
                            </TabPane>
                        </TabContent>
                    </CardBody>
                </Card>
            </Fragment>
        </>
    )
}
export default Ratings
